<template>
  <div>
    asd
  </div>

</template>

<script>
const typeConfig = {
  'text': '文本',
  'number': '数字',
  'boolean': '布尔值',
  'object': '对象',
  'array': '数组'
};

export default {
  name: "FooterSetting",
  mixins: [$mixins.propertyDialogSetting],
  data() {
    return {
      tableData: []
    };
  },
  filters: {
    typeFilter(to) {
      let res = [];
      _.forEach(to.split('|'), t => {
        let s = typeConfig[t];
        if (s) {
          res.push(s);
        }
      });
      return res.join('/');
    }
  },
  created() {
    if(this.currentData){
      this.tableData = _.cloneDeep(this.currentData);
    }
  },
  methods: {
    validate() {
      for (let i = 0, l = this.tableData.length; i < l; i++) {
        let { to, from } = this.tableData[i];
        if (!from) {
          dsf.layer.message(`${to}的映射键名不能为空`, false);
          return false;
        }
      }
    },
    yes() {
      return this.tableData;
    }
  }
}
</script>